@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* APP CSS */

*{
  box-sizing:border-box;
}
body, html{
  font-family: 'Open Sans';
}

a{
  color:#4285F4;;
  text-decoration: none;
}

.edit-icon{
  fill:#111;
  -webkit-transform:scale(1.8);
          transform:scale(1.8);
  cursor: pointer;
}

.admin-actions{
  position:fixed;
  z-index:1;
  width:100vw;
  top:10vh;
  left:0;
  height:5vh;
  display:grid;
  place-items:center;
  background:#eee;
}

main{
  min-height:100vh;
  display:grid;
  place-items:center;
  align-content:center;
  padding:20vh;
}

main.login img{
  width:100px;
}

label .add{
  text-align: center;
  padding:.3rem;
  width:8rem;
  border:1px solid #111;
}
 
@media (max-width:1000px){
  main{
    padding:20vh 5vh;
  }
}
@media (max-width:600px){
  main{
    padding:10vh 0 5vh 0;
  }
}

/* HEADER CSS */

.header-container{
  position:fixed;
  z-index:2;
  background:black;
  width:100vw;
  height:10vh;
  display:grid;
  place-items:center;
}
.burger{
  position:absolute;
  display:none;
  z-index:2;
}

header{
    width: 100%;
    height:100%;
    display:grid;
    grid-auto-flow: column;
    justify-content:center;
    align-items:center;
    grid-gap:1rem;
    gap:1rem;
    z-index:1;
  }
  
  header a{
    text-decoration: none;
    color:#eee;
  }
  
  [aria-current="page"], .active {
    font-weight: bold;
  }

  @media (max-width:1000px){
    header{
      background:black;
      grid-auto-flow:row;
      align-items: center;
      align-content:center;
      -webkit-transform:translateY(-100%);
              transform:translateY(-100%);
      min-height:100vh;
      -webkit-transition:.4s ease;
      transition:.4s ease;
    }
    header a{
      font-size:2rem;
    }
    header.mobile{    
      -webkit-transform:translateY(0);    
              transform:translateY(0);
    }
    .burger{
      display: block;
    }
  }
  
/* PROJECT CSS */

.project {
  width: 100%;
  min-height: 600px;
  position: relative;
  display:grid;
  place-items:center;
  overflow: hidden;
  cursor: pointer;
}

.project-inner{
  width:100%;
  height:100%;
  position:absolute;
  background:rgba(0,0,0,.2);
  display:grid;
  place-items:center;
  color:white;
  -webkit-transition:.6s ease;
  transition:.6s ease;
}

.project-inner:hover{
  background:rgba(0,0,0,0);
}
.project-inner:hover{
  background:rgba(0,0,0,0);
}
.project:hover > img{
  -webkit-transform:scale(1);
          transform:scale(1);
}

.project h1 {
  margin: .4rem 0 .2rem 0;
  padding: 0;
}

.project img {
  width: 100%;
  height:100%;
  object-fit: cover;
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
  -webkit-transition:.6s ease;
  transition:.6s ease;
}

.project-year {
  font-weight: bold;
}

.project-byline {
  text-transform: uppercase;
  padding: 1rem 0 1rem 0;
}

.project .admin {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  display: grid;
  grid-auto-flow: column;
  grid-gap: 1rem;
  gap: 1rem;
  font-size:2rem;
  color:white;
}

@media(max-width:600px){
  .project {
    min-height: 350px;
  }
}
main.projects{
    padding:15vh 0;
}
.projects-container{
    display:grid;
    width:100%;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
}

@media(max-width:600px){
    .projects-container{
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    }    
}
main.edit{
    max-width:100%;
    justify-items: left;
}

form{
    max-width:100%;
}

input,
button,
textarea,
label .add,
select {
    font-size: 1rem;
    padding: .5rem;
    display: block;
    margin-bottom: 1rem;
    cursor: pointer;
}

input,
textarea {
    min-width: 20rem;
    background: white;
}

textarea {
    width: 100%;
    min-height: 10rem;
}

button {
    color: white;
    background: black;
    min-width: 8rem;
}

.checks {
    background: #e6e6ea;
    margin: 3rem 0 3rem 0;
    max-width: 100%;
    display: grid;
    grid-template-columns: 2fr 3fr;
    justify-content: center;
    padding:.4rem;
}

.edit-images {
    display: grid;
    width:80%;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: .2rem;
    gap: .2rem;
    margin-bottom:1rem;
}

.edit-images > div {
    position: relative;
    display: grid;
    place-items: center;
}

.edit-images img {
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.edit-images h3 {
    color: #e6e6ea;
    position: absolute;
}

@media (max-width:600px){
    main.edit {
        padding:0 1rem 0 1rem;
    }
}

main.project-detail{
    padding:0;
    justify-content: start;
    background:white;
    position:relative;
}

.parallax-overlay{
    background:rgba(0,0,0,.2);
    width:100vw;
    height:100vh;
    padding-top:15vh;
    display:grid;
    place-items:center;
    color:#eee;
    font-size:2rem;
}

.parallax-overlay > div{
    position:-webkit-sticky;
    position:sticky;
    top:50vh;
}

.parallax-overlay h1{
    font-weight:300;
    padding:0;
    margin: 0;
}
.parallax-overlay p{
    font-weight:bold;
    margin:0 0 2rem 0;
}


.project-detail .project-content{
    min-height:100vh;
    padding:20vh;
}

.project-detail .project-images{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap:.2rem;
    gap:.2rem;
}

.project-images img{
    width:100%;
    height:300px;
    object-fit: cover;
}

.pager{
    display:grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: center;
    grid-gap:4rem;
    gap:4rem;
}
  
@media (max-width:1000px){
    .parallax-overlay{
        display:grid;
        place-items:center;
        color:#eee;
        padding:.2rem;
        font-size:1.4rem;
        text-align:center;
    }
    .parallax-overlay  ul{
        text-align:left;
        list-style: none;
    }
    .project-detail .project-content{
        min-height:100vh;
        padding:2vh;
    }
}
  @media (max-width:600px){
    main{
      padding:20vh 0 5vh 0;
    }
  }
  
main.accordion{
    padding:20vh;
}
main.accordion .menu{
    width:100%;
    justify-self: left;
}

.project-acc{
    width:100%;
}
.rc-collapse-item{
    width:100%;
    padding:1rem;
    background:#e6e6ea;
}
.rc-collapse-content{
    padding:.5rem;
}
